import React from 'react'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Link from '@material-ui/core/Link';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/Inbox';
import Typography from '@material-ui/core/Typography';
import './Extra.css'

const renderSupplementList = supplement => (
  <ListItem button>
    <ListItemIcon>
      <InboxIcon />
    </ListItemIcon>
    <ListItemText primary={ supplement.label } />
  </ListItem>
);

  // <li key={ supplement.label } style={ { display: 'flex' } }>
  //   <Typography component='h4'>{ supplement.label }</Typography>
  //   { 
  //     !!supplement.extraText &&
  //     <Typography component='h4'>{ `&nbsp;- ${supplement.extraText}` }</Typography>
  //   }
  // </li>

const renderGradeSupplements = gradeSupplement => {
  console.log('here2', gradeSupplement);
  return (
    <CardContent key={ gradeSupplement.grade }>
      <Typography variant='display1' component='h3'>{ gradeSupplement.grade }</Typography>
      <List>
        { gradeSupplement.links.map(renderSupplementList) }
      </List>
    </CardContent>
  );
};

const Extra = () => {
  return (
    <div id='Extra'>
      <Card style={ { backgroundColor: 'antiqueWhite', padding: '20px' } }>
        <Typography variant='display2' component='h2'>Here is some materials to supplement the curriculum:</Typography>
        { supplementalMaterials.map(grade => renderGradeSupplements(grade)) }
      </Card>
    </div>
  )
}

export default Extra;

var supplementalMaterials = [
  {
    grade: 'Kindergarten',
    links: [
      {
        label: 'Flash Cards',
        extraText: 'for helping your students',
        link: '',
      },
      {
        label: 'Vocabulary List',
        link: '',
      },
    ],
  },
  {
    grade: 'First Grade',
    links: [
      {
        label: 'Flashcards',
        link: '',
      },
      {
        label: 'Instructions to Beat the Kids',
        link: '',
      },
    ],
  },
];

